import { KeyValue } from '../../lib/keyboard'
import { getStatuses } from '../../lib/statuses'
import { Key } from './Key'
import { useEffect } from 'react'
import { ENTER_TEXT, DELETE_TEXT } from '../../constants/strings'

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  guesses: string[]
}

export const Keyboard = ({ onChar, onDelete, onEnter, guesses }: Props) => {
  const charStatuses = getStatuses(guesses)

  const onClick = (value: KeyValue) => {
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'DELETE') {
      onDelete()
    } else {
      onChar(value)
    }
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if (e.code === 'Enter') {
        onEnter()
      } else if (e.code === 'Backspace') {
        onDelete()
      } else {
        const key = e.key.toUpperCase()
        if (key.length === 1 && key >= 'A' && key <= 'Z') {
          onChar(key)
        }
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
  }, [onEnter, onDelete, onChar])

  return (
    <div>
      <div className="flex justify-center mb-1">
        <Key value="𐑐" onClick={onClick} status={charStatuses['𐑐']} />
        <Key value="𐑑" onClick={onClick} status={charStatuses['𐑑']} />
        <Key value="𐑒" onClick={onClick} status={charStatuses['𐑒']} />
        <Key value="𐑓" onClick={onClick} status={charStatuses['𐑓']} />
        <Key value="𐑔" onClick={onClick} status={charStatuses['𐑔']} />
        <Key value="𐑕" onClick={onClick} status={charStatuses['𐑕']} />
        <Key value="𐑖" onClick={onClick} status={charStatuses['𐑖']} />
        <Key value="𐑗" onClick={onClick} status={charStatuses['𐑗']} />
        <Key value="𐑘" onClick={onClick} status={charStatuses['𐑘']} />
        <Key value="𐑙" onClick={onClick} status={charStatuses['𐑙']} />
      </div>
      <div className="flex justify-center mb-1">
        <Key value="𐑚" onClick={onClick} status={charStatuses['𐑚']} />
        <Key value="𐑛" onClick={onClick} status={charStatuses['𐑛']} />
        <Key value="𐑜" onClick={onClick} status={charStatuses['𐑜']} />
        <Key value="𐑝" onClick={onClick} status={charStatuses['𐑝']} />
        <Key value="𐑞" onClick={onClick} status={charStatuses['𐑞']} />
        <Key value="𐑟" onClick={onClick} status={charStatuses['𐑟']} />
        <Key value="𐑠" onClick={onClick} status={charStatuses['𐑠']} />
        <Key value="𐑡" onClick={onClick} status={charStatuses['𐑡']} />
        <Key value="𐑢" onClick={onClick} status={charStatuses['𐑢']} />
        <Key value="𐑣" onClick={onClick} status={charStatuses['𐑣']} />
      </div>
      <div className="flex justify-center mb-1">
        <Key value="𐑤" onClick={onClick} status={charStatuses['𐑤']} />
        <Key value="𐑮" onClick={onClick} status={charStatuses['𐑮']} />
        <Key value="𐑥" onClick={onClick} status={charStatuses['𐑥']} />
        <Key value="𐑯" onClick={onClick} status={charStatuses['𐑯']} />
        <Key value="𐑦" onClick={onClick} status={charStatuses['𐑦']} />
        <Key value="𐑰" onClick={onClick} status={charStatuses['𐑰']} />
        <Key value="𐑧" onClick={onClick} status={charStatuses['𐑧']} />
        <Key value="𐑱" onClick={onClick} status={charStatuses['𐑱']} />
        <Key value="𐑨" onClick={onClick} status={charStatuses['𐑨']} />
        <Key value="𐑲" onClick={onClick} status={charStatuses['𐑲']} />
      </div>
      <div className="flex justify-center mb-1">
        <Key value="𐑩" onClick={onClick} status={charStatuses['𐑩']} />
        <Key value="𐑳" onClick={onClick} status={charStatuses['𐑳']} />
        <Key value="𐑪" onClick={onClick} status={charStatuses['𐑪']} />
        <Key value="𐑴" onClick={onClick} status={charStatuses['𐑴']} />
        <Key value="𐑫" onClick={onClick} status={charStatuses['𐑫']} />
        <Key value="𐑵" onClick={onClick} status={charStatuses['𐑵']} />
        <Key value="𐑬" onClick={onClick} status={charStatuses['𐑬']} />
        <Key value="𐑶" onClick={onClick} status={charStatuses['𐑶']} />
        <Key value="𐑭" onClick={onClick} status={charStatuses['𐑭']} />
        <Key value="𐑷" onClick={onClick} status={charStatuses['𐑷']} />
      </div>
      <div className="flex justify-center">
        <Key width={65.4} value="ENTER" onClick={onClick}>
          {ENTER_TEXT}
        </Key>
        <Key value="𐑸" onClick={onClick} status={charStatuses['𐑸']} />
        <Key value="𐑹" onClick={onClick} status={charStatuses['𐑹']} />
        <Key value="𐑺" onClick={onClick} status={charStatuses['𐑺']} />
        <Key value="𐑻" onClick={onClick} status={charStatuses['𐑻']} />
        <Key value="𐑼" onClick={onClick} status={charStatuses['𐑼']} />
        <Key value="𐑽" onClick={onClick} status={charStatuses['𐑽']} />
        <Key value="𐑾" onClick={onClick} status={charStatuses['𐑾']} />
        <Key value="𐑿" onClick={onClick} status={charStatuses['𐑿']} />
        <Key width={65.4} value="DELETE" onClick={onClick}>
          {DELETE_TEXT}
        </Key>
      </div>
    </div>
  )
}
